import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Platform, ActionSheetController, AlertController } from 'ionic-angular';


@Component({
  selector: 'page-pakage1',
  templateUrl: 'pakage1.html',
})
export class Pakage1Page {
  pet: string = "puppies";
  isAndroid: boolean = false;

  constructor(public navCtrl: NavController, public navParams: NavParams, platform: Platform,
    public actionSheetCtrl: ActionSheetController, public alertCtrl: AlertController) {
    this.isAndroid = platform.is('android');
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad Pakage1Page');
  }

  presentActionSheet() {
    const actionSheet = this.actionSheetCtrl.create({
      title: 'Método de pago',
      buttons: [
        {
          text: 'Comprar con tarjeta de crédito',
          handler: () => {
            this.showConfirm(1);
          }
        },{
          text: 'Generar código efecty',
          handler: () => {
            this.showConfirm(2);
          }
        },{
          text: 'Generar código baloto',
          handler: () => {
            this.showConfirm(3);
          }
        },{
          text: 'Cancelar',
          role: 'cancel',
          handler: () => {
            console.log('Cancel clicked');
          }
        }
      ]
    });
    actionSheet.present();
  }

  showConfirm(option: number) {
 let  msg='';
     if(option == 1){
        msg= 'El descuento se aplicara a su tarjeta de crédito';
     }else if(option == 2){
        msg='Se ha generado el código 123 para aplicar a el convenio efecty 34567'
     }else{
       msg='Se ha generado el código 123 para aplicar a el convenio baloto 93527'
     }

    const confirm = this.alertCtrl.create({
      title: 'deseas adquirir este paquete?',
      message: msg ,
      buttons: [
        {
          text: 'Cancelar',
          handler: () => {
            console.log('Disagree clicked');
          }
        },
        {
          text: 'Aceptar',
          handler: () => {
            console.log('Agree clicked');
          }
        }
      ]
    });
    confirm.present();
  }

}



