import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { ListPage } from '../pages/list/list';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { WhoIamPage } from '../pages/who-iam/who-iam';
import {EthicalValuesPage} from '../pages/ethical-values/ethical-values';
import { ServicesPage } from '../pages/services/services';
import { Pakage1Page } from '../pages/pakage1/pakage1';
import { Pakage2Page } from '../pages/pakage2/pakage2';
import { Pakage3Page } from '../pages/pakage3/pakage3';
import { TipsPage } from '../pages/tips/tips';
import { ChatPage } from '../pages/chat/chat';
import { EmojiProvider } from '../providers/chat-service/emoji';
import { ChatService } from '../providers/chat-service/chat-service';
import { RelativeTimePipe } from '../pipes/relative-time/relative-time';
import { EmojiPickerComponent } from '../components/emoji-picker/emoji-picker';
import { HttpClient, HttpClientModule } from '@angular/common/http';
import { RegisterPage } from '../pages/register/register';


@NgModule({
  declarations: [
    MyApp,
    HomePage,
    ListPage,
    WhoIamPage,
    EthicalValuesPage,
    ServicesPage,
    Pakage1Page,
    Pakage2Page,
    Pakage3Page,
    TipsPage,
    ChatPage,
    RelativeTimePipe,
    EmojiPickerComponent,
    RegisterPage
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    IonicModule.forRoot(MyApp),
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    ListPage,
    WhoIamPage,
    EthicalValuesPage,
    ServicesPage,
    Pakage1Page,
    Pakage2Page,
    Pakage3Page,
    TipsPage,
    ChatPage,
    RegisterPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    ChatService,
    EmojiProvider,
    HttpClient
  ]
})
export class AppModule {}



