import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Pakage1Page } from '../pakage1/pakage1';
import { Pakage2Page } from '../pakage2/pakage2';
import { Pakage3Page } from '../pakage3/pakage3';

@Component({
  selector: 'page-services',
  templateUrl: 'services.html',
})
export class ServicesPage {

  items = [
    {name:"Parque la Poma",id:1},
    {name:"Humedal Tibanica",id:2},
    {name:"Parque Canoas",id:3}
  ];

  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ServicesPage');
  }

  itemSelected(item: string){
    if(item=="1"){
      this.navCtrl.push(Pakage1Page);
    }else if(item=="2"){
      this.navCtrl.push(Pakage2Page)
    }else{
      this.navCtrl.push(Pakage3Page)
    } 
  }

}



